/**
 * HOVER
 *
 * Cuando desarrollas una aplicacin que puede funcionar para pantallas
 * tctiles, puedes tener problemas con los efectos hover, el mixin
 * hover hace que solo se aplicaquen en dispositivos no tctiles.
 */
@mixin hover {
  @media not all and (pointer: coarse) {
    &:hover:not(:disabled) {
      @content;
    }
  }
}
