//
// Modo de visualizacin
//
// Modifica el modo en que se visualiza un elemento.
//

//
// Las clases se forman con el siguiente formato:
//
// .display-{breakpoint}-{value}
//
// {breakpoint} Indica a que tamao de pantalla afecta
//   - palm, lap, small, desk, large
//   - Si no se indica ningn tamao de pantalla, se aplica a todos
// {value} Indica el tamao del margen
//   - none, inline, inline-block, block
//
// Ejemplos:
//   .display-none         // Oculta un elemento
//   .display-palm-none    // Oculta un elemento solo en tamaos de pantalla mviles
//

@each $breakpoint in $breakpoints-list {
  @include media($breakpoint) {
    $breakpoint: get-breakpoint-sufix($breakpoint);

    .display#{$breakpoint}-none {
      display: none !important;
    }
    .display#{$breakpoint}-inline {
      display: inline !important;
    }
    .display#{$breakpoint}-inline-block {
      display: inline-block !important;
    }
    .display#{$breakpoint}-block {
      display: block !important;
    }
  }
}

//
// Modo de visualizacin en impresin
//
// Modifica el modo en que se visualiza un elemento en modo impresin
//

//
// Las clases se forman con el siguiente formato:
//
// .display-print-{value}
//
// {value} Indica el tamao del margen
//   - none, inline, inline-block, block
//
// Ejemplos:
//   .display-print-none     // Oculta un elemento al imprimir la pgina
//   .display-print-block    // Muestra un elemento como bloque al imprimir la pgina
//
@if $print {
  .display-print-none {
    @media print {
      display: none !important;
    }
  }

  .display-print-inline {
    @media print {
      display: inline !important;
    }
  }

  .display-print-inline-block {
    @media print {
      display: inline-block !important;
    }
  }

  .display-print-block {
    @media print {
      display: block !important;
    }
  }
}
